'use client'

import { Vehicle, csvToVehicles, vehiclesToCSV, getVehicleStatus } from './csv-handler'

class DataService {
  private vehicles: Vehicle[] = []
  private initialized = false

  async initialize() {
    if (this.initialized) return
    
    try {
      // Try to load from localStorage first
      const savedData = localStorage.getItem('vehicles_data')
      if (savedData) {
        this.vehicles = JSON.parse(savedData)
      } else {
        // Load initial data from CSV file
        const response = await fetch('/data/vehicles.csv')
        const csvContent = await response.text()
        this.vehicles = csvToVehicles(csvContent)
        this.saveToLocalStorage()
      }
      
      // Update vehicle statuses
      this.vehicles = this.vehicles.map(vehicle => ({
        ...vehicle,
        status: getVehicleStatus(vehicle)
      }))
      
      this.initialized = true
    } catch (error) {
      console.error('Failed to initialize data service:', error)
      this.vehicles = []
      this.initialized = true
    }
  }

  private saveToLocalStorage() {
    localStorage.setItem('vehicles_data', JSON.stringify(this.vehicles))
  }

  async getAllVehicles(): Promise<Vehicle[]> {
    await this.initialize()
    return [...this.vehicles]
  }

  async getVehicleById(id: string): Promise<Vehicle | null> {
    await this.initialize()
    return this.vehicles.find(v => v.id === id) || null
  }

  async createVehicle(vehicleData: Omit<Vehicle, 'id' | 'createdAt' | 'updatedAt' | 'status'>): Promise<Vehicle> {
    await this.initialize()
    
    const newVehicle: Vehicle = {
      ...vehicleData,
      id: this.generateId(),
      status: getVehicleStatus(vehicleData as Vehicle),
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    }
    
    this.vehicles.push(newVehicle)
    this.saveToLocalStorage()
    
    return newVehicle
  }

  async updateVehicle(id: string, updates: Partial<Vehicle>): Promise<Vehicle | null> {
    await this.initialize()
    
    const index = this.vehicles.findIndex(v => v.id === id)
    if (index === -1) return null
    
    const updatedVehicle = {
      ...this.vehicles[index],
      ...updates,
      id, // Ensure ID doesn't change
      updatedAt: new Date().toISOString()
    }
    
    updatedVehicle.status = getVehicleStatus(updatedVehicle)
    this.vehicles[index] = updatedVehicle
    this.saveToLocalStorage()
    
    return updatedVehicle
  }

  async deleteVehicle(id: string): Promise<boolean> {
    await this.initialize()
    
    const index = this.vehicles.findIndex(v => v.id === id)
    if (index === -1) return false
    
    this.vehicles.splice(index, 1)
    this.saveToLocalStorage()
    
    return true
  }

  async getExpiringVehicles(daysThreshold: number = 30): Promise<Vehicle[]> {
    await this.initialize()
    return this.vehicles.filter(vehicle => 
      vehicle.status === 'expiring_soon' || vehicle.status === 'expired'
    )
  }

  async getVehicleStats() {
    await this.initialize()
    
    const total = this.vehicles.length
    const active = this.vehicles.filter(v => v.status === 'active').length
    const expiringSoon = this.vehicles.filter(v => v.status === 'expiring_soon').length
    const expired = this.vehicles.filter(v => v.status === 'expired').length
    
    return { total, active, expiringSoon, expired }
  }

  async exportToCSV(): Promise<string> {
    await this.initialize()
    return vehiclesToCSV(this.vehicles)
  }

  async importFromCSV(csvContent: string): Promise<{ success: boolean; message: string; imported: number }> {
    try {
      const importedVehicles = csvToVehicles(csvContent)
      
      if (importedVehicles.length === 0) {
        return { success: false, message: 'No valid vehicles found in CSV', imported: 0 }
      }
      
      // Update existing vehicles or add new ones
      let imported = 0
      for (const vehicle of importedVehicles) {
        const existingIndex = this.vehicles.findIndex(v => v.id === vehicle.id)
        if (existingIndex >= 0) {
          this.vehicles[existingIndex] = {
            ...vehicle,
            status: getVehicleStatus(vehicle),
            updatedAt: new Date().toISOString()
          }
        } else {
          this.vehicles.push({
            ...vehicle,
            status: getVehicleStatus(vehicle),
            updatedAt: new Date().toISOString()
          })
        }
        imported++
      }
      
      this.saveToLocalStorage()
      return { success: true, message: 'Import successful', imported }
    } catch (error) {
      return { success: false, message: 'Failed to parse CSV file', imported: 0 }
    }
  }

  async clearAllData(): Promise<void> {
    this.vehicles = []
    localStorage.removeItem('vehicles_data')
  }

  private generateId(): string {
    return Date.now().toString(36) + Math.random().toString(36).substr(2)
  }
}

// Create singleton instance
export const dataService = new DataService()

// React hook for using the data service
import { useState, useEffect } from 'react'

export function useVehicles() {
  const [vehicles, setVehicles] = useState<Vehicle[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const refreshVehicles = async () => {
    try {
      setLoading(true)
      setError(null)
      const data = await dataService.getAllVehicles()
      setVehicles(data)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load vehicles')
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    refreshVehicles()
  }, [])

  return {
    vehicles,
    loading,
    error,
    refreshVehicles,
    createVehicle: async (data: Omit<Vehicle, 'id' | 'createdAt' | 'updatedAt' | 'status'>) => {
      const newVehicle = await dataService.createVehicle(data)
      await refreshVehicles()
      return newVehicle
    },
    updateVehicle: async (id: string, updates: Partial<Vehicle>) => {
      const updated = await dataService.updateVehicle(id, updates)
      await refreshVehicles()
      return updated
    },
    deleteVehicle: async (id: string) => {
      const success = await dataService.deleteVehicle(id)
      if (success) await refreshVehicles()
      return success
    }
  }
}

export function useVehicleStats() {
  const [stats, setStats] = useState({ total: 0, active: 0, expiringSoon: 0, expired: 0 })
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const loadStats = async () => {
      try {
        const data = await dataService.getVehicleStats()
        setStats(data)
      } catch (error) {
        console.error('Failed to load stats:', error)
      } finally {
        setLoading(false)
      }
    }

    loadStats()
  }, [])

  return { stats, loading }
}
