'use client'

import { useState, useEffect } from 'react'
import { useParams, useRouter } from 'next/navigation'
import { useAuth } from '@/lib/auth'
import { useLanguage } from '@/lib/language'
import { dataService } from '@/lib/data-service'
import { Vehicle } from '@/lib/csv-handler'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { Alert, AlertDescription } from '@/components/ui/alert'
import Link from 'next/link'

export default function VehicleDetailPage() {
  const params = useParams()
  const router = useRouter()
  const { isAuthenticated, hasPermission } = useAuth()
  const { language, t } = useLanguage()
  const [vehicle, setVehicle] = useState<Vehicle | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    if (!isAuthenticated) {
      router.push('/login')
      return
    }

    const loadVehicle = async () => {
      try {
        setLoading(true)
        setError(null)
        const vehicleData = await dataService.getVehicleById(params.id as string)
        if (vehicleData) {
          setVehicle(vehicleData)
        } else {
          setError(language === 'ar' ? 'المركبة غير موجودة' : 'Vehicle not found')
        }
      } catch (err) {
        setError(language === 'ar' ? 'خطأ في تحميل البيانات' : 'Failed to load vehicle data')
      } finally {
        setLoading(false)
      }
    }

    loadVehicle()
  }, [params.id, isAuthenticated, router, language])

  useEffect(() => {
    document.documentElement.lang = language
    document.documentElement.dir = language === 'ar' ? 'rtl' : 'ltr'
  }, [language])

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'active':
        return <Badge variant="secondary" className="bg-green-100 text-green-800">{t('vehicles.active')}</Badge>
      case 'expiring_soon':
        return <Badge variant="secondary" className="bg-yellow-100 text-yellow-800">{t('vehicles.expiring_soon')}</Badge>
      case 'expired':
        return <Badge variant="destructive">{t('vehicles.expired')}</Badge>
      default:
        return <Badge variant="outline">{status}</Badge>
    }
  }

  const isDateExpired = (dateString: string) => {
    return new Date(dateString) < new Date()
  }

  const isDateExpiringSoon = (dateString: string) => {
    const expiryDate = new Date(dateString)
    const today = new Date()
    const diffTime = expiryDate.getTime() - today.getTime()
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
    return diffDays <= 30 && diffDays > 0
  }

  const getDateClassName = (dateString: string) => {
    if (isDateExpired(dateString)) return 'text-red-600 font-semibold'
    if (isDateExpiringSoon(dateString)) return 'text-yellow-600 font-semibold'
    return 'text-green-600'
  }

  const handleDelete = async () => {
    if (!vehicle) return
    
    if (confirm(`${t('message.confirm_delete')} ${vehicle.assetName}?`)) {
      try {
        await dataService.deleteVehicle(vehicle.id)
        router.push('/vehicles')
      } catch (error) {
        console.error('Failed to delete vehicle:', error)
      }
    }
  }

  if (!isAuthenticated) {
    return null
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
          <p>{language === 'ar' ? 'جاري التحميل...' : 'Loading...'}</p>
        </div>
      </div>
    )
  }

  if (error || !vehicle) {
    return (
      <div className="space-y-4">
        <Alert variant="destructive">
          <AlertDescription>{error || (language === 'ar' ? 'المركبة غير موجودة' : 'Vehicle not found')}</AlertDescription>
        </Alert>
        <Link href="/vehicles">
          <Button variant="outline">{language === 'ar' ? 'العودة للمركبات' : 'Back to Vehicles'}</Button>
        </Link>
      </div>
    )
  }

  return (
    <div className="space-y-6 print-area">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div className="flex items-center gap-4">
          <Link href="/vehicles" className="print:hidden">
            <Button variant="outline" size="sm">
              {language === 'ar' ? '← العودة' : '← Back'}
            </Button>
          </Link>
          <div>
            <h1 className="text-3xl font-bold">{vehicle.assetName}</h1>
            <p className="text-muted-foreground">{vehicle.model} - {vehicle.year}</p>
          </div>
          {getStatusBadge(vehicle.status)}
        </div>
        
        <div className="flex gap-2 print:hidden">
          <Button 
            variant="outline" 
            onClick={() => window.print()}
          >
            {language === 'ar' ? 'طباعة البيانات' : 'Print Asset Data'}
          </Button>
          {hasPermission('admin') && (
            <>
              <Link href={`/vehicles/${vehicle.id}/edit`}>
                <Button variant="outline">{t('vehicles.edit')}</Button>
              </Link>
              <Button variant="destructive" onClick={handleDelete}>
                {t('vehicles.delete')}
              </Button>
            </>
          )}
        </div>
      </div>

      {/* Vehicle Images */}
      <Card>
        <CardHeader>
          <CardTitle>{t('vehicles.images')}</CardTitle>
        </CardHeader>
        <CardContent>
          {vehicle.images && vehicle.images.length > 0 ? (
            <div className="grid grid-cols-1 gap-4">
              {vehicle.images.map((image, index) => (
                <div key={index} className="aspect-video relative overflow-hidden rounded-lg">
                  <img
                    src={image}
                    alt={`صورة المركبة ${vehicle.assetName} رقم ${index + 1}`}
                    className="w-full h-full object-contain rounded-md"
                    onError={(e) => {
                      e.currentTarget.src = 'https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/24faee54-a937-4d1c-959a-9c9f66892a00.png'
                    }}
                  />
                </div>
              ))}
            </div>
          ) : (
            <div className="aspect-video bg-muted rounded-lg flex items-center justify-center">
              <p className="text-muted-foreground">
                {language === 'ar' ? 'لا توجد صور' : 'No images available'}
              </p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Vehicle Information */}
      <Card>
        <CardHeader>
          <CardTitle>{language === 'ar' ? 'معلومات المركبة' : 'Vehicle Information'}</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.assetName')}</label>
              <p className="font-medium">{vehicle.assetName}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.assetNo')}</label>
              <p className="font-medium text-blue-600 font-bold">{vehicle.assetNo}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.brand')}</label>
              <p className="font-medium">{vehicle.brand}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.model')}</label>
              <p className="font-medium">{vehicle.model}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.vinNo')}</label>
              <p className="font-medium text-amber-700 font-bold">{vehicle.vinNo}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.year')}</label>
              <p className="font-medium">{vehicle.year}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.status')}</label>
              <div className="mt-1">{getStatusBadge(vehicle.status)}</div>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.plateNo')}</label>
              <p className="font-medium text-purple-800 font-bold">{vehicle.plateNo}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.description')}</label>
              <p className="font-medium">{vehicle.description}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.assetType')}</label>
              <p className="font-medium">{vehicle.assetType}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.location')}</label>
              <p className="font-medium">{vehicle.location}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.color')}</label>
              <p className="font-medium">{vehicle.color}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.assignedUser')}</label>
              <p className="font-medium">{vehicle.assignedUser}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.owner')}</label>
              <p className="font-medium">{vehicle.owner}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.insuranceStatus')}</label>
              <p className="font-medium">{vehicle.insuranceStatus}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.operationCardStatus')}</label>
              <p className="font-medium">{vehicle.operationCardStatus}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.insurance')}</label>
              <p className="font-medium">{vehicle.insuranceExpiry}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.license')}</label>
              <p className="font-medium">{vehicle.licenseExpiry}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.inspection')}</label>
              <p className="font-medium">{vehicle.inspectionExpiry}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">{t('vehicles.operationCard')}</label>
              <p className="font-medium">{vehicle.operationCardExpiry}</p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
