export interface Vehicle {
  id: string
  assetNo: string
  assetName: string
  brand: string
  model: string
  vinNo: string
  year: string
  plateNo: string
  color: string
  description: string
  assetType: string
  assignedUser: string
  owner: string
  status: 'active' | 'expired' | 'expiring_soon'
  location: string
  insuranceStatus: 'active' | 'expired' | 'expiring_soon'
  operationCardStatus: 'active' | 'expired' | 'expiring_soon'
  insuranceExpiry: string
  licenseExpiry: string
  inspectionExpiry: string
  operationCardExpiry: string
  images: string[]
  documents: Document[]
  createdAt: string
  updatedAt: string
}

export interface Document {
  id: string
  name: string
  type: string
  expiryDate: string
  filePath: string
  uploadedAt: string
}

// CSV parsing and formatting utilities
export function parseCSV(csvContent: string): any[] {
  const lines = csvContent.trim().split('\n')
  if (lines.length === 0) return []
  
  const headers = lines[0].split(',').map(h => h.trim().replace(/"/g, ''))
  const data = []
  
  for (let i = 1; i < lines.length; i++) {
    const values = parseCSVLine(lines[i])
    if (values.length === headers.length) {
      const row: any = {}
      headers.forEach((header, index) => {
        row[header] = values[index]
      })
      data.push(row)
    }
  }
  
  return data
}

function parseCSVLine(line: string): string[] {
  const result = []
  let current = ''
  let inQuotes = false
  
  for (let i = 0; i < line.length; i++) {
    const char = line[i]
    
    if (char === '"') {
      inQuotes = !inQuotes
    } else if (char === ',' && !inQuotes) {
      result.push(current.trim())
      current = ''
    } else {
      current += char
    }
  }
  
  result.push(current.trim())
  return result
}

export function formatCSV(data: any[]): string {
  if (data.length === 0) return ''
  
  const headers = Object.keys(data[0])
  const csvLines = [headers.join(',')]
  
  data.forEach(row => {
    const values = headers.map(header => {
      let value = row[header] || ''
      // Handle arrays and objects
      if (Array.isArray(value)) {
        value = JSON.stringify(value)
      } else if (typeof value === 'object') {
        value = JSON.stringify(value)
      }
      // Escape quotes and wrap in quotes if contains comma
      value = String(value).replace(/"/g, '""')
      if (value.includes(',') || value.includes('"') || value.includes('\n')) {
        value = `"${value}"`
      }
      return value
    })
    csvLines.push(values.join(','))
  })
  
  return csvLines.join('\n')
}

// Vehicle-specific CSV operations
export function vehiclesToCSV(vehicles: Vehicle[]): string {
  const flattenedVehicles = vehicles.map(vehicle => ({
    id: vehicle.id,
    assetNo: vehicle.assetNo,
    assetName: vehicle.assetName,
    brand: vehicle.brand,
    model: vehicle.model,
    vinNo: vehicle.vinNo,
    year: vehicle.year,
    plateNo: vehicle.plateNo,
    color: vehicle.color,
    description: vehicle.description,
    assetType: vehicle.assetType,
    assignedUser: vehicle.assignedUser,
    owner: vehicle.owner,
    status: vehicle.status,
    location: vehicle.location,
    insuranceStatus: vehicle.insuranceStatus,
    operationCardStatus: vehicle.operationCardStatus,
    insuranceExpiry: vehicle.insuranceExpiry,
    licenseExpiry: vehicle.licenseExpiry,
    inspectionExpiry: vehicle.inspectionExpiry,
    operationCardExpiry: vehicle.operationCardExpiry,
    images: JSON.stringify(vehicle.images),
    documents: JSON.stringify(vehicle.documents),
    createdAt: vehicle.createdAt,
    updatedAt: vehicle.updatedAt
  }))
  
  return formatCSV(flattenedVehicles)
}

export function csvToVehicles(csvContent: string): Vehicle[] {
  const data = parseCSV(csvContent)
  
  return data.map(row => ({
    id: row.id || generateId(),
    assetNo: row.assetNo || '',
    assetName: row.assetName || '',
    brand: row.brand || '',
    model: row.model || '',
    vinNo: row.vinNo || '',
    year: row.year || '',
    plateNo: row.plateNo || '',
    color: row.color || '',
    description: row.description || '',
    assetType: row.assetType || '',
    assignedUser: row.assignedUser || '',
    owner: row.owner || '',
    status: row.status || 'active',
    location: row.location || '',
    insuranceStatus: row.insuranceStatus || 'active',
    operationCardStatus: row.operationCardStatus || 'active',
    insuranceExpiry: row.insuranceExpiry || '',
    licenseExpiry: row.licenseExpiry || '',
    inspectionExpiry: row.inspectionExpiry || '',
    operationCardExpiry: row.operationCardExpiry || '',
    images: parseJSONField(row.images) || [],
    documents: parseJSONField(row.documents) || [],
    createdAt: row.createdAt || new Date().toISOString(),
    updatedAt: row.updatedAt || new Date().toISOString()
  }))
}

function parseJSONField(field: string): any {
  try {
    return JSON.parse(field || '[]')
  } catch {
    return []
  }
}

function generateId(): string {
  return Date.now().toString(36) + Math.random().toString(36).substr(2)
}

// Date utility functions
export function isExpiringSoon(dateString: string, daysThreshold: number = 30): boolean {
  if (!dateString) return false
  
  const expiryDate = new Date(dateString)
  const today = new Date()
  const diffTime = expiryDate.getTime() - today.getTime()
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
  
  return diffDays <= daysThreshold && diffDays > 0
}

export function isExpired(dateString: string): boolean {
  if (!dateString) return false
  
  const expiryDate = new Date(dateString)
  const today = new Date()
  
  return expiryDate < today
}

export function getVehicleStatus(vehicle: Vehicle): 'active' | 'expired' | 'expiring_soon' {
  const expiryDates = [
    vehicle.insuranceExpiry,
    vehicle.licenseExpiry,
    vehicle.inspectionExpiry,
    vehicle.operationCardExpiry
  ].filter(date => date)
  
  if (expiryDates.some(date => isExpired(date))) {
    return 'expired'
  }
  
  if (expiryDates.some(date => isExpiringSoon(date))) {
    return 'expiring_soon'
  }
  
  return 'active'
}

// File system operations (browser-based)
export function downloadCSV(content: string, filename: string) {
  const blob = new Blob([content], { type: 'text/csv;charset=utf-8;' })
  const link = document.createElement('a')
  
  if (link.download !== undefined) {
    const url = URL.createObjectURL(blob)
    link.setAttribute('href', url)
    link.setAttribute('download', filename)
    link.style.visibility = 'hidden'
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }
}

export function readCSVFile(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = (e) => {
      const content = e.target?.result as string
      resolve(content)
    }
    reader.onerror = () => reject(new Error('Failed to read file'))
    reader.readAsText(file)
  })
}
